.portfolio__content.case__active{
    display: block;
}

.portfolio__content{
    display: none;
    width: 100%;
    height: 999px;
    position: fixed;
    overflow: auto;
    top: 0;
    left: 0;
    z-index: 9999999999;
    background: linear-gradient(106.03deg, #132324 0%, #284143 64.31%, #132223 99.74%), #FFFFFF;
}

.pc__header{
    #portfolio{
        width: 100%;
        margin: 0;
        display: flex;
        grid-template-columns: none;
        height: auto;
        justify-content: space-between;
        margin-top: vw(80);
    }
}

.pc__container{
    overflow: auto;
}

.pc__content{
    
    .swiper{

        .swiper-wrapper{


            .swiper-slide{
                width: 100%;
                height: auto;
                padding-top: vw(160);

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }

                &::after{
                    display: none;
                }

                .work__header{
                    width: 100%;
                    height: auto;
                    border: none;
                    background: transparent;
                    backdrop-filter: none;
                    position: absolute;
                    transform: translateX(-50%);
                    left: 50%;
                    top: vw(0);
                    z-index: 40;
                    padding: 0;
                    display: flex;
                    justify-content: center;
                    flex-direction: column;
                    text-align: center;
            
                    &::after{
                        display: none;
                    }
            
                    &::before{
                        display: none;
                    }
                }
            }
        }

    }

}

.sbc__wrapp{
    display: flex;
    justify-content: space-between;
    position: absolute;
    width: vw(930);
    left: 50%;
    transform: translateX(-50%);
    top: vw(200);
    z-index: 999999999999;
}

.swiper-button-content-prev, .swiper-button-content-next{
    position: relative;
    cursor: pointer;
    width: vw(74);
    height: vw(74);
    z-index: 999999999999;

    &.disable{
        opacity: .5;
    }

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &::before{
        content: '';
        position: absolute;
        inset: 0;
        border-radius: 50%;
        padding: vw(3);
        width: vw(60);
        height: vw(60);
        background: radial-gradient(50% 50% at 50% 50%, rgba(153, 233, 235, 0) 86.98%, rgba(153, 233, 235, 0.21) 100%);
        -webkit-mask: 
            linear-gradient(white 0 0) content-box, 
            linear-gradient(white 0 0);
        -webkit-mask-composite: xor;
        mask-composite: exclude;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .btn__arrow{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        .btn__img{
            width: vw(8);
            height: vw(14);

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}

.swiper-button-content-prev{
    img{
        transform: rotate(180deg);
    }

    .btn__arrow{
        .btn__img{
            img{
                transform: rotate(0deg);
            }
        }
    }
}

.close__content{
    width: vw(42);
    height: vw(42);
    border-radius: 50%;
    background: radial-gradient(50% 50% at 50% 50%, rgba(153, 233, 235, 0) 86.98%, rgba(153, 233, 235, 0.21) 100%);
    position: absolute;
    right: vw(235);
    top: vw(160);
    padding-top: vw(12);
    padding-left: vw(20);
    cursor: pointer;

    &::after{
        content: '';
        position: absolute;
        width: vw(2);
        height: vw(19);
        background-color: #48CCCF;
        transform: rotate(45deg);
    }

    &::before{
        content: '';
        position: absolute;
        width: vw(2);
        height: vw(19);
        background-color: #48CCCF;
        transform: rotate(135deg);
    }
}

.pc__footer{
    margin: vw(60) 0;
    display: flex;
    align-items: center;
    flex-direction: column;

    a.back{
        margin-top: vw(40);

        p{
            margin-left: vw(20);
            color: rgba($color: #fff, $alpha: 0.6);
            font-family: $MS-Regular;
            font-size: vw(16);
        }

        span{
            position: relative;

            &::after{
                content: '';
                position: absolute;
                width: vw(2.5);
                height: vw(12);
                background-color: rgba($color: #fff, $alpha: 0.6);
                transform: rotate(45deg);
            }

            &::before{
                content: '';
                position: absolute;
                width: vw(2.5);
                height: vw(12);
                background-color: rgba($color: #fff, $alpha: 0.6);
                transform: rotate(135deg);
                bottom: vw(-18);
            }
        }
    }
}